<template>
    <div class="liked">
        
        <div class="liked-header">
            <span class="iconfont"><span class="liked-guess">&#xe60e;</span> 猜你喜欢</span>
        </div>
        
        <ul class="liked-ul">
            <li class="liked-li" v-for="item in list" :key="item.id">
                <div class="liked-img">
                    <img class="liked-img-1" :src="item.imgUrl" />
                    <div class="" v-if="item.tips==='a'">
                        <img class="liked-img-2" src="https://img1.qunarzz.com/piao/fusion/1802/20/2ba6d10b17972e02.png" />
                        <span class="liked-img-2-note">随买随用</span>
                    </div>
                    <div class="" v-else-if="item.tips === 't'">
                        <img class="liked-img-2" src="https://img1.qunarzz.com/piao/fusion/1802/52/b9080e45b69b4f02.png" />
                        <span class="liked-img-2-note">可订明日</span>
                    </div>
                </div>
                <div class="liked-desc">
                    <h1 class="liked-title">{{ item.title }}</h1>
                    <span class="liked-star">★★★★★</span>
                    <span class="liked-com">
                        <em class="liked-com-num">{{ item.commentNum }}</em> 评论
                    </span>
                    <p class="liked-price">
                        <span class="liked-yuan">
                            <em class="liked-price-em">¥{{ item.price }}</em>起
                        </span>
                        <span class="liked-area">{{ item.area }}</span>
                        </p>
                    <span class="liked-comment">{{ item.comment }}</span>    
                </div>
            </li>
        </ul>

        <div class="liked-more">
            <a href="/" class="liked-more-a">查看所有产品</a>
        </div>
    </div>
</template>

<script>
export default {
    name: 'HomeLiked',
    props: {
        list: Array
    }
}
</script>

<style lang="stylus" scoped>
    .liked 
        overflow hidden
        display flex
        flex-wrap wrap
        background-color #eee
        .liked-header   
            width 100%
            margin-top .2rem
            background-color #fff
            padding .24rem .26rem
            .liked-guess
                color red
                font-weight 700 
        .liked-ul
            display flex
            flex-wrap wrap
            background-color #fff
            .liked-li
                display flex
                flex-wrap wrap 
                border-bottom 1px solid #eee
                width 100%
                .liked-img
                    position relative
                    width 2rem
                    height 2rem
                    padding .24rem .26rem
                    .liked-img-1
                        width 100% 
                    .liked-img-2
                        position absolute
                        top .24rem            
                        width 1.02rem
                        height .39rem  
                    .liked-img-2-note
                        position relative
                        top -2rem 
                        left 0 
                        font-size .22rem
                        color #fff
                .liked-desc        
                    flex 1
                    padding .24rem .26rem
                    .liked-title
                        font-size .32rem
                        padding .16rem 0
                    .liked-star
                        color #ff8300    
                    .liked-com
                        font-size .24rem
                        padding 0 .86rem
                    .liked-price
                        display flex
                        justify-content space-between
                        padding .24rem 0
                        font-size .24rem
                        .liked-price-em                    
                            color #ff8300
                            font-size .32rem
                    .liked-area
                        font-size .28rem 
                    .liked-comment
                        color red
                        font-size .24rem       
        .liked-more
            background-color #fff
            height .4rem
            width 100%
            padding .24rem 0
            text-align center
            .liked-more-a
                display block
                height 100%
                line-height 100%
                padding .24rem 0
                width 50%
                margin 0 auto
</style>


