<template>
    <div>
        <div class="search">
            <input class="search-input" type="text" v-model="searchContent" placeholder="请输入城市名或拼音" />
        </div>

        <div class="search-content" ref="item" v-show="searchContent">
            <ul>
                <li class="search-item" v-for="item in list" :key="item.id" @click="storeClick(item.name)">{{ item.name }}</li>
                <li class="search-item" v-show="hasNotData">没有找到你输入的城市</li>
            </ul>            
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'

export default {
    name: 'CitySearch',
    props: {
        cities: Object
    },
    data () {
        return {
            searchContent: '',
            list: [],
            timer: null
        }
    },
    methods: {
        storeClick (val) {
            this.$store.commit('change', val)
            this.$router.push('/')
        }
    },
    computed: {
        hasNotData () {
            return !this.list.length
        }
    },
    watch: {
        searchContent () {
            if (this.timer) {
                clearTimeout(this.timer)
            }
            if (!this.searchContent) {
                this.list = []
                return
            }
            this.timer = setTimeout( () => {
                let result = []
                for (let i in this.cities) {
                    this.cities[i].forEach(element => {
                        if (element.spell.indexOf(this.searchContent) > -1 || element.name.indexOf(this.searchContent) > -1 ) {
                            result.push(element)
                        }
                    });
                    this.list = result
                }
            }, 100)
        }
    },
    mounted () {
        this.scroll = new BScroll(this.$refs.item)
    },
}
</script>

<style lang="stylus" scoped>
    @import '~@/assets/style/comm.styl'
    .search
        height .72rem
        background $bgColor
        text-align center
        padding 0 .1rem
        .search-input
            box-sizing border-box
            height .64rem
            line-height .64rem
            width 100%;
            padding 0 .1rem
            text-align center
            border-radius .06rem
            color #666
    .search-content
        z-index 1
        overflow hidden
        position absolute
        top 1.58rem
        left 0
        right 0
        bottom 0   
        background #fff
        .search-item
            list-style none
            padding .2rem .1rem
            border-bottom 1px solid #eee
</style>

