<template>
  <div class="icons">
    <swiper :options="swiperOption" class="swiper">
      <swiper-slide v-for="(page, index) in pages" :key="index">
        <div class="icon"  v-for="item in page" :key="item.id">
          <div class="icon-img">
            <img class="icon-img-content" :src="item.imgUrl" />    
          </div>
          <p class="icon-desc">{{ item.desc }}</p>
        </div>    
      </swiper-slide>
      <div class="swiper-pagination swiper-point"  slot="pagination"></div>
    </swiper>
  </div>    
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data () {
      return {
        swiperOption: {
            pagination: {
              el: '.swiper-pagination'
            },
        }
    }
  },
  computed: {
    pages () {
      const pages = []
      const MAX = 8
      this.list.forEach((element, index) => {
        const page = Math.floor(index / MAX)
        if (!pages[page]) {
          pages[page] = []    
        }        
        pages[page].push(element)
      });
      return pages
    }
  }    
}
</script>

<style lang="stylus" scoped>
  @import '~@/assets/style/comm.styl'
  @import '~@/assets/style/mixins.styl'

  .icons >>> .swiper-container 
    // overflow: hidden
    height: 3.7rem
    margin-top .1rem
  .icons 
    .swiper-point  
      margin 0 auto
    .icon 
      position: relative
      overflow: hidden
      float: left
      width: 25% 
      height: 1.56rem
      .icon-img
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom: .44rem
        width 1.2rem
        height 1.2rem
        margin 0 auto
        box-sizing: border-box
        padding-top .1rem
        .icon-img-content
          display: block
          margin: 0 auto 
          height: 100%  
      .icon-desc  
          position: absolute
          left: 0
          right: 0
          bottom: 0
          font-size .28rem
          margin-top .1rem
          text-align center   
          color $darkColor          
          ellipsis()
</style>
