<template>
    <div class="list" ref="wrapper">
        <div class="content">
            <div class="area">
                <h3 class="title">当前城市</h3>
                <div class="city">
                    <div class="postion">
                        <p>{{ currentPosition }}</p>  
                    </div>
                </div>
            </div>

            <div class="area">
                <h3 class="title">热门城市</h3>
                <div class="city-hot">
                    <div class="postion" 
                         v-for="item in hot" 
                         :key="item.id"
                    >
                        <p @click="storeClick(item.name)">{{ item.name }}</p>  
                    </div>
                </div>
            </div>

            <div class="area" 
                 v-for="(val, key) in cities"
                 :key="key"
                 :ref="key" 
            >
                <h3 class="title">{{ key }}</h3>
                <div class="city">
                    <div class="item" 
                         v-for="item in val"
                         :key="item.id"   
                    >
                        <p class="city-name" @click="storeClick(item.name)">{{ item.name }}</p>
                    </div>   
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapState, mapMutations } from 'vuex'

export default {
    name: 'CityList',
    props: {
        hot: Array,
        cities: Object,
        letter: String
    },
    watch: {
        letter () {
            const ele = this.$refs[this.letter]
            this.scroll.scrollToElement(ele[0])
        }
    },
    methods: {
        storeClick (val) {
            this.change(val)
            this.$router.push('/')
        },
        ...mapMutations(['change'])
    },
    computed: {
        ...mapState({
            currentPosition: 'position'
        })
    },
    mounted () {
        this.scroll = new BScroll(this.$refs.wrapper)
    }
}
</script>

<style lang="stylus" scoped>
    @import '~@/assets/style/comm.styl'
   
    .list
        position absolute 
        width 100%
        top 1.58rem
        left 0
        right 0
        bottom 0
        overflow hidden
        .content
            float left
            .area
                float left
                width 100%  
                .city-hot
                    margin-right .6rem
                .postion
                    float left
                    width 1.58rem
                    height .4rem
                    line-height .4rem
                    border 1px solid #eee;
                    border-radius .06rem
                    text-align center
                    margin .1rem
                .item
                    width 100%
                    border-bottom 1px solid #eee
                    .city-name
                        padding .24rem .1rem   
                .title
                    background #eee
                    height .46rem
                    line-height .46rem
                    padding .1rem
                
</style>

