<template>
  <div class="wrapper">
    <swiper :options="swiperOption" v-if="showSwiper">
      <swiper-slide v-for="item in list" :key="item.id"><img class="swiper-img" :src="item.imgUrl" /></swiper-slide>    
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>    
  </div>
</template>

<script>
export default {
    name: 'HomeSwiper',
    props: {
        list: Array
    },
    data () {
        return {
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination'
                },
                loop: true,
                autoplay: true
            }
            // data here
        }
    },
    computed: {
        showSwiper () {
            return this.list.length
        }
    }
}
</script>

<style scoped lang="stylus">
  .wrapper >>> .swiper-pagination-bullet-active //>>>表示全局可用
    background: #fff      
  .wrapper
    overflow: hidden
    width: 100%
    height: 0
    padding-bottom: 26.67%
    background: #eee
    .swiper-img 
      width: 100%  
</style>