<template>
    <div class="city">
        <city-header></city-header>
        <city-search :cities="cities"></city-search>
        <city-list :cities="cities" :hot="hotCities" :letter="letter"></city-list>
        <city-alphabet :cities="cities" @change="letterChange"></city-alphabet>
    </div>
</template>

<script>

import CityHeader from '@/components/City/Header.vue'
import CitySearch from '@/components/City/Search.vue'
import CityList from '@/components/City/List.vue'
import CityAlphabet from '@/components/City/Alphabet.vue'

export default {
    name: 'City',
    components: {
        CityHeader,
        CitySearch,
        CityList,
        CityAlphabet
    },
    data () {
        return {
            cities: {},
            hotCities: [],
            letter: ''
        }
    },
    methods: {
        letterChange (letter) {
            this.letter = letter
        },
        getCityInfoSucc (res) {

            res = res.data

            if (res.ret && res.data) {

                const data = res.data
                this.cities = data.cities
                this.hotCities = data.hotCities
            }
        },
        getCityInfo () {
            this.$axios.get("/mock/city.json").then( 
                this.getCityInfoSucc
            )
        }
    },
    mounted () {
        this.getCityInfo()
    }
}
</script>

