<template>
    <ul class="alphabet">
        <li class="item" 
            v-for="item in letters" 
            :key="item"
            :ref="item"
            @touchstart.prevent="handleTouchStart"
            @touchmove="handleTouchMove"
            @touchend="handleTouchEnd"
            @click="letterChange"
        >
            {{ item }}
        </li>
    </ul>
</template>

<script>
export default {
    name: 'CityAlphabet',
    props: {
        cities: Object
    },
    computed: {
         letters () {
             const letters = []
             for (let i in this.cities) {
                 letters.push(i)
             }
            return letters
         }
    },
    updated () {
        this.startY = this.$refs['A'][0].offsetTop
    },
    data () {
        return {
            touchStatus: false,
            startY: 0,
            timer: null
       }
    },
    methods: {
        letterChange (e) {
            this.$emit('change', e.target.innerText)
        },
        handleTouchStart () {
            this.status = true
        },
        handleTouchMove (e) {
            if (this.status) {
                if (this.timer) {
                    clearTimeout(this.timer)
                }
                this.timer = setTimeout(() => {
                    const clientY = e.touches[0].clientY -79
                    const index = Math.floor((clientY - this.startY) / 19)
                    
                    if (index >= 0 && index < this.letters.length) {
                        this.$emit('change', this.letters[index])
                    }
                }, 16)
            }
        },
        handleTouchEnd () {
            this.touchStatus = false
        }
    }
}
</script>

<style lang="stylus" scoped>

    @import '~@/assets/style/comm.styl'
    .alphabet
        display flex
        flex-direction column
        justify-content center
        position absolute        
        top 1.58rem
        right 0
        bottom 0
        width .4rem
        .item
            text-align center
            padding .05rem 0
            color $bgColor
            
</style>

