<template>
  <div class="home">
    <home-header></home-header>
    <home-swiper :list="swiperList"></home-swiper>
    <home-icons :list="iconList"></home-icons>
    <home-list></home-list>
    <home-activity></home-activity>
    <home-hot :list="hotList"></home-hot>
    <home-liked :list="likedList"></home-liked>
    <home-weekend :list="weekendList"></home-weekend>
    <home-price></home-price>
    <home-footer></home-footer>
  </div>
</template>

<script>
// @ is an alias to /src
import HomeHeader from '@/components/Home/Header.vue'
import HomeSwiper from '@/components/Home/Swiper.vue'
import HomeIcons from '@/components/Home/Icons.vue'
import HomeList from '@/components/Home/List.vue'
import HomeActivity from '@/components/Home/Activity.vue'
import HomeHot from '@/components/Home/Hot.vue'
import HomeLiked from '@/components/Home/Liked.vue'
import HomeWeekend from '@/components/Home/Weekend.vue'
import HomePrice from '@/components/Home/Price.vue'
import HomeFooter from '@/components/Home/Footer.vue'

import { mapState } from 'vuex'

export default {
  name: 'home',
  components: {
    HomeHeader,
    HomeSwiper,
    HomeIcons,
    HomeList,
    HomeActivity,
    HomeHot,
    HomeLiked,
    HomeWeekend,
    HomePrice,
    HomeFooter
  },
  data () {
    return {
      lastPosition: '',
      swiperList: [],
      iconList: [],
      weekendList: [],
      likedList: [],
      hotList: []
    }
  },
  methods: {
    getHomeInfo () {
      this.$axios.get('/mock/index.json?city=' + this.position).then (
        this.getHomeInfoSucc
      )
    },

    getHomeInfoSucc (res) {
      
      res = res.data      
      
      if (res.ret && res.data) {
        const data = res.data
        this.swiperList = data.swiperList
        this.iconList = data.iconList
        this.weekendList = data.weekendList
        this.likedList = data.likedList
        this.hotList = data.hotList
      }
    }
  },
  computed: {
    ...mapState(['position'])
  },
  mounted () {
    this.getHomeInfo()
  },
  activated () {
    if (this.lastPosition !== this.position) {
      this.getHomeInfo ()
      this.lastPosition = this.position
    }
  }
}
</script>
